.assortment {
  font-size: 19px;
  font-weight: 700;
  line-height: 1.3;
  min-height: 205px;
  width: $mobile-width;
  padding-bottom: 28px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 1.5;
    min-height: 119px;
    width: 668px;
    margin-left: 50px;
    margin-top: -128px;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    width: 1050px;
    padding: 0;
  }
}

.assortment__wrapper {
  @include style-none;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    padding-bottom: 44px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
    padding-bottom: 124px;
  }
}

.assortment__wrapper::before {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    content: "";
    position: absolute;
    width: 770px;
    height: 24px;
    margin-top: 107px;
    margin-left: -52px;
    background-image: url("/img/bg-zigzag-fill.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 772px;
    z-index: 1;
  }

  @media (min-width: $desktop-width) {
    display: block;
    content: "";
    position: absolute;
    width: 1220px;
    height: 34px;
    margin-top: 102px;
    margin-left: -108px;
    background-image: url("/img/bg-zigzag-fill.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 1265px;
  }
}


.assortment__wrapper a {
  color: $main-color;
  font-weight: $bold-font-weight;
}

.assortment__interior,
.assortment__toys {
  position: relative;
  height: 104px;
  width: $mobile-width;
  background-color: $mint-color;

  &:hover,
  &:focus {
    margin-top: -5px;
    height: 109px;

    a {
      margin-top: 23px;
    }
  }

  &:active {
    a {
      opacity: 0.3;
    }
  }

  @media (min-width: $tablet-width) {
    height: 120px;
    width: 334px;

    &:hover,
    &:focus {
      margin-top: -5px;
      height: 125px;

      a {
        margin-top: 23px;
      }
    }
  }

  @media (min-width: $desktop-width) {
    width: 525px;
  }
}

.assortment__toys {
  background-color: $dark-mint-color;
}

.assortment__interior span,
.assortment__toys span {
  display: inline-block;
  padding-top: 26px;
  padding-left: 28px;
  width: 140px;

  @media (min-width: $tablet-width) {
    padding-top: 29px;
    padding-left: 36px;
  }
}

.assortment__interior-icon {
  position: absolute;
  top: 24px;
  right: 36px;
  fill: #29a592;

  @media (min-width: $tablet-width) {
    top: 30px;
  }
}

.assortment__toys-icon {
  position: absolute;
  top: 21px;
  right: 27px;
  fill: #29a592;

  @media (min-width: $tablet-width) {
    top: 32px;
    right: 34px;
  }
}
